// Single page/post/project

// - - - - - - - - - - - - - - - - - -

.intro {
	margin-top: 0;
	padding: 20px 0 0;

	@include mq(tabletp) {
		padding: 35px 0 0;
	}

	@include mq(tabletl) {
		padding: 35px 40px 0;
	}

	@include mq(laptop) {
		padding: 53px 60px 0;
	}

	.wrap {

		@include mq(tabletl) {
			max-width: 80%;
		}

		@include mq(laptop) {
			max-width: 90%;
		}

		@include mq(desktop) {
		}
	}
	
	h1 {
		
		@include mq(tabletp) {
			white-space: pre-line;
		}
	}

	p {
		margin-top: 5px;
		color: $text-light-color;
	}
}

.single {
	margin-top: 0;
	padding: 20px 0;
	min-height: 100vh;

	@include mq(tabletp) {
		padding: 40px 0;
	}

	@include mq(tabletl) {
		padding: 30px 40px 40px;
	}

	@include mq(laptop) {
		padding: 40px 60px 60px;
	}

	h1, h2, h3, h4, h5, h6, p, ul, ol {
		margin-left: auto;
		margin-right: auto;

		@include mq(tabletp) {
		}

		@include mq(tabletl) {
			max-width: 90%;
		}

		@include mq(laptop) {
			max-width: 90%;
		}

		@include mq(desktop) {
			max-width: 90%;
		}
	}

	hr {
		margin: 20px auto;

		@include mq(tabletp) {
			margin-top: 40px;
			margin-bottom: 40px;
			width: auto;
		}

		@include mq(tabletl) {
			max-width: 90%;
		}

		@include mq(laptop) {
			max-width: 90%;
		}

		@include mq(desktop) {
			max-width: 90%;
		}
	}

	blockquote,
	figure {
		margin: 20px auto;

		@include mq(tabletp) {
			margin: 30px auto;
		}

		@include mq(laptop) {
			margin: 40px auto;
		}
	}

	blockquote p {
		max-width: 90%;
		padding: 0;
	}

	p a {
		font-weight: $bold-weight;
		border-bottom: 2px solid $accent-color;
	}

	p .button {
		margin-top: 10px;
	}

	.highlighter-rouge {
		margin: 20px auto;
		width: 100%;

		@include mq(tabletp) {
			margin: 30px auto;
		}

		@include mq(tabletl) {
			max-width: 80%;
		}

		@include mq(laptop) {
			margin: 40px auto;
			max-width: 75%;
		}

		@include mq(desktop) {
			max-width: 70%;
		}
	}

	.highlight {
		display: block;
		padding: 20px;
		line-height: 1.5;

		@include mq(tabletp) {
			padding: 30px;
		}

		@include mq(laptop) {
			padding: 40px;
		}

		.highlight {
			padding: 0;
		}
	}
}

.image-wrap,
.video-wrap {
	position: relative;
	margin: 20px auto;

	@include mq(tabletp) {
		margin: 30px auto;
	}

	@include mq(laptop) {
		margin: 40px auto;
	}

	&:first-child {
		margin-top: 0;
	}
}

.image-wrap img {
	margin: 0 auto;
}

.video {
	position: relative;
	width: 100%;
	height: 0;

	iframe {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}
}
